import { AccountInfo, Connection, PublicKey } from '@solana/web3.js';
import { chunks } from './chunks';

export async function chunkedGetMultipleAccountInfos(
  connection: Connection,
  pks: PublicKey[],
  chunkSize: number = 100,
): Promise<Array<AccountInfo<Buffer> | null>> {
  return (
    await Promise.all(
      chunks(
        pks.map((item) => item.toBase58()),
        chunkSize,
      ).map((chunk) => {
        const args = connection._buildArgs([chunk], connection.commitment, 'base64');
        return (
          // getMultipleAccounts is quite slow, so we use fetch directly
          connection
            // @ts-ignore
            ._rpcRequest('getMultipleAccounts', args)
            .then((item: any) => {
              const accounts = item.result.value as Array<AccountInfo<string[]> | null>;
              accounts.forEach((item) => {
                if (item) {
                  const value = item as unknown as AccountInfo<Buffer>;
                  value.data = Buffer.from(item.data[0], item.data[1] as 'base64');
                  value.owner = new PublicKey(value.owner);
                }
              });

              return accounts;
            })
            .catch((e: any) => {
              console.error('Failed to fetch account infos', e);
              return chunk.map(() => null);
            })
        );
      }),
    )
  ).flat();
}
